body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.images {
    width: 80%;
    margin: auto;
    background-color: white;
    border: 2px solid #ccc;
    margin-top: 5%;
}
.clicky-image {
    width: 200px;
    height: 200px;
    margin: 30px;
    border: 4px solid lightblue;
    border-radius: 3px;
    z-index: 1
}

.clicky-image:hover {
    position: relative;
    width: 220px;
    height: auto;
    margin-top: 5px;
    margin-left: 10px;
    z-index: 50;
    cursor: pointer;
}
#status-bar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: fixed;
    top: 0;
    width: 100%;
    background-color: lightblue;
    margin-top: 0;
    color: #222;
    overflow: auto;
    clear: both;
}


#chem-clickey {
    font-size: 2rem;
    font-weight: bold;
    margin-left: 10px;
}
#status-msg {
    font-size: 2rem;

}
ul {
    list-style: none;
}

#scores-list {
    display: inline-flex;
    color: #222;
    font-size: 2rem;
    margin-top: 5px;
    padding-left: 0
}

#score-text {
    font-weight: bold;
    font-size: 2rem;
    color: #222;
    padding-right: 10px;
}

#high-score-text {
    font-weight: bold;
    font-size: 2rem;
    color: #222;
    margin-right: 15px;
}
@media (max-width: 1050px) {
    #status-msg {
        font-size: 1.5rem;
    }

    #score-text, #high-score-text, #scores-list, #chem-clickey {
        font-size: 1.5rem;
    }
}






.App {
    text-align: center;
    color: #444;
}

.app-header {
  margin-top: 8%;
}

.instructions {
  text-align: left;
  padding-left: 30%;
  line-height: .75rem;
  margin-top: 2%;
}

@media (max-width: 1360px) {
  .app-header {
    margin-top: 15%;
  }
}
@media (max-width: 650px) {
  .app-header {
    margin-top: 25%;
  }
}
